import * as WebBrowser from 'expo-web-browser';

import FacebookIcon from '@material-ui/icons/Facebook';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import TwitterIcon from '@material-ui/icons/Twitter';

import LinksContent from '../components/Contents/LinksContent';

const BUTTONS = [
  {
    Icon: () => <FacebookIcon />,
    label: 'social:facebook.link',
    onPress: () =>
      WebBrowser.openBrowserAsync('https://www.facebook.com/thetechintel'),
  },
  {
    Icon: () => <LinkedInIcon />,
    label: 'social:linkedIn.link',
    onPress: () =>
      WebBrowser.openBrowserAsync('https://linkedin.com/company/thetechintel'),
  },
  {
    Icon: () => <TwitterIcon />,
    label: 'social:twitter.link',
    onPress: () => WebBrowser.openBrowserAsync('https://twitter.com/5ervant'),
  },
];

export default function SocialLinks() {
  return <LinksContent buttons={BUTTONS} />;
}
