import React from 'react';
import { StyleSheet, Text, View } from 'react-native';
import { RectButton } from 'react-native-gesture-handler';
import { useTranslation } from 'react-i18next';

import SafeScrollView from '../Safe/SafeScrollView';

import Styles, { scrollViewContentContainer } from '../../constants/Styles';

export default function LinksContent({ buttons, headerHeight }) {
  const { t } = useTranslation();

  return (
    <SafeScrollView
      style={Styles.container}
      contentContainerStyle={scrollViewContentContainer(headerHeight)}
    >
      {buttons.map((button, i, arr) => {
        const isLastOption = arr.length - 1 === i;

        return (
          <OptionButton
            key={i}
            Icon={button.Icon}
            label={button.label}
            onPress={button.onPress}
            isLastOption={isLastOption}
            t={t}
          />
        );
      })}
    </SafeScrollView>
  );
}

function OptionButton({ Icon, label, onPress, isLastOption, t }) {
  return (
    <RectButton
      style={[styles.option, isLastOption && styles.lastOption]}
      onPress={onPress}
    >
      <View style={{ flexDirection: 'row' }}>
        <View style={styles.optionIconContainer}>
          <Icon />
        </View>
        <View style={styles.optionTextContainer}>
          <Text style={styles.optionText}>{t(label)}</Text>
        </View>
      </View>
    </RectButton>
  );
}

const styles = StyleSheet.create({
  optionIconContainer: {
    marginRight: 12,
  },
  option: {
    backgroundColor: '#fdfdfd',
    paddingHorizontal: 15,
    paddingVertical: 15,
    borderWidth: StyleSheet.hairlineWidth,
    borderBottomWidth: 0,
    borderColor: '#ededed',
  },
  lastOption: {
    borderBottomWidth: StyleSheet.hairlineWidth,
  },
  optionText: {
    fontSize: 15,
    alignSelf: 'flex-start',
    marginTop: 1,
  },
});
