import { StyleSheet } from 'react-native';

const Styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fafafa'
  },
  contentContainer: {
    paddingVertical: 15
  }
});

export default Styles;

export function scrollViewContentContainer(headerHeight) {
  return [
    Styles.contentContainer,
    { paddingTop: headerHeight + Styles.contentContainer.paddingVertical }
  ];
}
